import { createContext, useContext, useEffect, useReducer, useState } from 'react'
// import authReducer from './AuthReducer'

const authReducer = (state, action) => {
  switch (action.type) {
      case "LOGIN_START":
          return {
              ...state,
              user: null,
              isLoading: true,
              error: false,
          }
      case "LOGIN_SUCCESS":
          return {
              user: action.payload,
              isLoading: false,
              error:  false,
              register_pending: false
          }
      case "LOGIN_FAILURE":
          return {
              ...state,
              user: null,
              isLoading: false,
              error: true,
          }
      case "LOGOUT":
          return {
              user: null,
              isLoading: false,
              error: false,
              register_pending: false
          }
        case "REGISTER_START":
          return {
              ...state,
              isLoading:true,
              register_pending: false
          }
      case "REGISTER_SUCCESS":
          return {
            ...state,
              register_pending: true,
          }
      case "REGISTER_FAILURE":
          return {
            ...state,
            register_pending: false
          }
      case "UPDATE_START":
          return {
              ...state,
              isLoading:true,
          }
      case "UPDATE_SUCCESS":
          return {
                ...state,
              user: action.payload,
              isLoading: false,
              error:  false,
          }
      case "UPDATE_FAILURE":
          return {
            ...state,
              user: state.user,
              isLoading: false,
              error: true,
          }
      default:
          return state;
      
  }
}

const INITIAL_STATE = {
    user: JSON.parse(localStorage.getItem("user")) || null,
    isLoading: false,
    error: false,
    register_pending: false
}
export const AuthContext = createContext(INITIAL_STATE)


export const AuthProvider = ({children}) => {
  const [state, dispatch] = useReducer(authReducer, INITIAL_STATE)

  useEffect(() => {
    localStorage.setItem("user", JSON.stringify(state.user))
}, [state.user])

  return (
    <AuthContext.Provider
    value={{
        user: state.user,
        isFetching: state.isLoading,
        error:state.error,
        register_pending: state.register_pending,
        dispatch,
    }}>
        {!state.isLoading && children}
    </AuthContext.Provider>
  )
}

export const useAuth = () => {
  return useContext(AuthContext)
}

export default AuthContext